const MyApi = {};
MyApi.install = function (Vue, options) {
    // urls: ; obj: ;
    // type: (get); headers: 
    Vue.prototype.$HTTP = function (type, urls, obj, load) {
         if(load) {
          var loading = this.$loading({
            target: load,
            lock: true,
            text: 'Loading',
            spinner: 'el-icon-loading',
            background: 'rgba(255,255,255, 0.3)'
          });
        }
        let obj1 = Object.assign({}, obj);
        let key = "4951841F25AF0C695331B3F3C0369089";
        for(let x in obj) {
            obj[x] = encodeURIComponent(obj[x]);
        }
       
        obj1.key = key;
        obj.sign = objSortJoin(obj1);
        if (type === 'post') {
            let axios = {
                method: type,
                url: urls === 'file' ? '/img.ashx' : this.global.urlPath + urls,
                data: this.$qs.stringify(obj),
                headers: {
                    'X-Requested-With': 'XMLHttpRequest',
                    'Content-Type': urls === 'file' ? 'application/x-www-form-urlencoded' : 'application/x-www-form-urlencoded; charset=UTF-8'
                }
            };
            return this.$axios(axios).then(res => {
                if(load) {
                  loading.close();
                }
                return res.data;
                
            }).catch(err => {
                if(load) {
                    loading.close();
                }
            });
        }
        if (type === 'get') {
            return this.$axios({
                method: type,
                url: this.global.urlPath + urls,
                params: obj,
                headers: {
                    'X-Requested-With': 'XMLHttpRequest'
                }
            }).then(res => {
                // if(load) {
                //   loading.close();
                // }
                return res.data;
            });
        }
    }
    var UTF8 = (str) => {
        if(window.TextEncoder) {
        var encoder = new TextEncoder('utf8');
        var bytes = encoder.encode(str);
        var result = '';
        for(var i = 0; i < bytes.length; ++i) {
        result += String.fromCharCode(bytes[i]);
        }
        return result;
        }else {
        return eval('\''+encodeURI(str).replace(/%/gm, '\\x')+'\'');
        }
        }
    // 
    var objSortJoin = (obj) => {
        let upperJSONKey = (jsonObj) => {
            for (var key in jsonObj){
                jsonObj[key.toUpperCase()] = jsonObj[key];
                delete(jsonObj[key]);
            }
            return jsonObj;
        }
        let arys = upperJSONKey(obj);
        var newkey = Object.keys(arys).sort();
        var newObj = {}; //
        for (var i = 0; i < newkey.length; i++) {
          //newkey
          newObj[newkey[i]] = arys[newkey[i]];
          //
        }
        let str = '';    
        for (let i in newObj) {
            newObj[i] = encodeURIComponent(newObj[i]);
            str += i + '=' + newObj[i] + '&';
        }
        str = str.slice(0, str.length - 1);
        str = str.toLocaleUpperCase(); 
        str = Vue.prototype.$md5(str);
        str = str.toLocaleUpperCase();
        return str; //
    }


    // 
    Vue.prototype.format = (dt, type) => {
        let str = ""; //
        //
        let year = dt.getFullYear();
        //
        let month = dt.getMonth() + 1;
        //
        let day = dt.getDate();
        //
        let hour = dt.getHours();
        //
        let min = dt.getMinutes();
        //
        let sec = dt.getSeconds();
        month = month < 10 ? "0" + month : month;
        day = day < 10 ? "0" + day : day;
        hour = hour < 10 ? "0" + hour : hour;
        min = min < 10 ? "0" + min : min;
        sec = sec < 10 ? "0" + sec : sec;
        if(!type) {
            return str = year + "-" + month + "-" + day + " " + hour + ":" + min + ":" + sec;
        }
        if(type == 'yyyy-MM-ddHH:mm:ss') {
            return str = year + "-" + month + "-" + day + " " + hour + ":" + min + ":" + sec;
        }
        if(type == 'yyyy/MM/ddHH:mm:ss') {
            return str = year + "/" + month + "/" + day + " " + hour + ":" + min + ":" + sec;
        }
        if(type == 'yyyy-MM-ddHH:mm') {
            return str = year + "-" + month + "-" + day + " " + hour + ":" + min;
        }
        if(type == 'yyyy/MM/ddHH:mm') {
            return str = year + "/" + month + "/" + day + " " + hour + ":" + min;
        }
        if(type == 'yyyy-MM-dd') {
            return str = year + "-" + month + "-" + day;
        }
        if(type == 'yyyy/MM/dd') {
            return str = year + "/" + month + "/" + day;
        }
        if(type == 'HH:mm:ss') {
            return str = hour + ":" + min + ":" + sec;
        }
        if(type == 'HH:mm') {
            return str = hour + ":" + min;
        }
        return str = year + "-" + month + "-" + day + " " + hour + ":" + min + ":" + sec;
    }

    // 
    Vue.prototype.conver = (limit) => {
        var size = "";
        if (limit < 0.1 * 1024) { //0.1KBB
            size = limit.toFixed(2) + "B";
        } else if (limit < 0.1 * 1024 * 1024) {//0.1MBKB
            size = (limit / 1024).toFixed(2) + "KB";
        } else if (limit < 0.1 * 1024 * 1024 * 1024) { //0.1GBMB
            size = (limit / (1024 * 1024)).toFixed(2) + "MB";
        } else { //GB
            size = (limit / (1024 * 1024 * 1024)).toFixed(2) + "GB";
        }

        var sizestr = size + "";
        var len = sizestr.indexOf("\.");
        var dec = sizestr.substr(len + 1, 2);
        if (dec == "00") {//00 
            return sizestr.substring(0, len) + sizestr.substr(len + 3, 2);
        }
        return sizestr;
    }

    // 
    Vue.prototype.getSuffix = (filename) => {
        var index1 = filename.lastIndexOf(".");
        var index2 = filename.length;
        var suffix = filename.substring(index1 + 1, index2);//
        return suffix;
    }

    // 
    Vue.prototype.getFlieTyle = (surffix) => {
        surffix = surffix.toLocaleLowerCase();
        if (surffix === 'png' || surffix === 'jpeg' || surffix === 'jpg' || surffix === 'gif' || surffix === 'bmp') {
            return 1;
        } else if (surffix === 'doc' || surffix === 'docx') {
            return 2; // 
        } else if (surffix === 'xls' || surffix === 'xlsx') {
            return 3; // 
        } else if (surffix === 'psd') {
            return 4; // PS
        } else if (surffix === 'ppt' || surffix === 'pptx') {
            return 5; // ppt
        } else if (surffix === 'pdf') {
            return 6; // pdf
        } else if (surffix === 'mp3' || surffix === 'wma' || surffix === 'wav' || surffix === 'ogg' || surffix === 'ra') {
            return 7; // 
        } else if (surffix === 'wmv' || surffix === 'avi' || surffix === 'mpg' || surffix === 'rmvb' || surffix === 'mp4') {
            return 8; //
        } else if (surffix === 'zip' || surffix === 'rar') {
            return 9; // 
        } else if (surffix === 'ai') {
            return 10; // ai
        } else if (surffix === 'txt') {
            return 11; // txt
        } else if (surffix === 'folder') {
            return 12; // txt
        } else {
            return 0; // 
        }
    }


    // 
    Vue.prototype.timeDiff = (time) => {
        if(!time) {
            return {
                state: '',
                date: '',
            }
        }
        //
        // var dateBegin = new Date(time.replace(/-/g, "/"));//-/new Date
        var dateBegin = new Date(time);//-/new Date
        var dateEnd = new Date();//
        var dateDiff = dateEnd.getTime() - dateBegin.getTime();//
        var dayDiffAbs = Math.abs(dateDiff);

        var dayDiff = Math.floor(dayDiffAbs / (24 * 3600 * 1000));//
        var leave1=dayDiffAbs%(24*3600*1000)    //
        var hours=Math.floor(leave1/(3600*1000))//
        //
        var leave2=leave1%(3600*1000)    //
        var minutes=Math.floor(leave2/(60*1000))//
        //
        var leave3=leave2%(60*1000)      //
        var seconds=Math.round(leave3/1000) //
   
        // 
        //
        let year = dateBegin.getFullYear();
        let year1 = dateEnd.getFullYear();
        //
        let month = dateBegin.getMonth() + 1;
        let month1 = dateEnd.getMonth() + 1;
        month = month < 10 ? '0' + month : month;
        month1 = month1 < 10 ? '0' + month1 : month1;

        //
        let day = dateBegin.getDate();
        let day1 = dateEnd.getDate();

        // 
        let H = dateBegin.getHours();
        H = H < 10 ? '0' + H : H;

        // 
        let Min = dateBegin.getMinutes();
        Min = Min < 10 ? '0' + Min : Min;


        let dd = new Date();
        dd.setDate(dd.getDate() - 2);
        let isQT = dd.getDate();
        if(isQT === day && dd.getMonth() + 1 === parseInt(month)) {
            isQT = true;
        }else {
            isQT = false;
        }

        let dd1 = new Date();
        dd1.setDate(dd1.getDate() - 1);
        let isZT = dd1.getDate();
        if(isZT === day && dd1.getMonth() + 1 ===  parseInt(month)) {
            isZT = true;
        }else {
            isZT = false;
        }

        let dd2 = new Date();
        dd2.setDate(dd2.getDate() - 3);
        let isLong = dd2.getDate();
        if(isLong === day && dd2.getMonth() + 1 ===  parseInt(month)) {
            isLong = true;
        }else {
            isLong = false;
        }

        // 1.
        // 2.XX
        // 3.XX24
        // 4.
        // 5.
        // 6.
        // 7.
        // console.log(time, dateDiff, "  "+dayDiff+" "+hours+" "+minutes+" "+seconds+" ");

        if(year !== year1) {
            return year + '/' + month + '/' + day + ' ' + H + ':' + Min;
        }else if(dayDiff >= 3 || isLong) {
            return month + '/' + day + ' ' + H + ':' + Min;
        }else if(isQT) {
            return ' ' + H + ':' + Min;
        }else if(isZT) {
            return ' ' + H + ':' + Min;
        }else if(dayDiff == 0 && day == day1) {
            if(hours > 0) {
                if(minutes > 0 || seconds > 0) {
                    return (hours + 1) + '';
                }else {
                    return hours + '';
                }
            }else if(minutes > 0){
                return minutes + '';
            }else {
                return '';
            }
            if(minutes > 0 || seconds > 0) {
                return (hours + 1) + '';
            }else {
                return hours + '';
            }
        }
        return;
        if(dateDiff <= 0) { // 
            if(dayDiff == 0 && day == day1) { //  state = 2
                return {
                    countDown: ( minutes > 0 || seconds > 0 ) ? '' + (hours + 1) + '' : '' + hours + '',
                    state: 2,
                    date: '',
                }

            }else { //  state = 4;
                return {
                    state: 4,
                    date: month + '' + day + '',
                }
            }
        }else { //  state = 3
           
            if((dayDiff > 98) && (hours > 0 || minutes > 0 || seconds > 0)) {
                return {
                    state: 3,
                    date: '99+',
                }
            }else {
                return {
                    state: 3,
                    date: (hours > 0 || minutes > 0 || seconds > 0) ? '' + (dayDiff + 1) + '' : '' + dayDiff + '',
                }
            }
        }
 
        
        // console.log(dateDiff+"",dayDiff+"",leave1+""
        //     ,hours+"",minutes+"",seconds+"");
            

    }
}
export default MyApi;



