          this.inputText = '';
        }
      }else { // 
        this.inputTextShow2 = true;
        if(this.inputTextShow1) {
          this.inputTextShow1 = false;
          this.inputText = '';
        }
      }
      this.$nextTick(() => {
        $('#inputText').focus();
      });
      let clickHide = e => {
