        this.fileDownlod1(item);
        return;
      }
     
      if(type === 'rename') { // 
        item.edit = true;
        this.fileNameCopy = item.FileTitle;
        this.personalFiles = this.personalFiles.concat();
        this.editFocus('#fileNameEdit');
        return;
      }
      if(type === 'delete') { // 
        this.delFileFlag1 = true;
