      }
      this.filePartitionId = id;
      if(id) {
        this.uploadFrom = 4;
      }else {
        this.uploadFrom = 2;
      }
    },
    // 
    handleClickUpload(groupId) {
      if(groupId === 'personal') {
        this.uploadFrom = 5;
        return;
      }
      this.filePartitionId = groupId;
      if(groupId) {
        this.uploadFrom = 3;
      }else {
