import Vue from 'vue'
import Vuex from 'vuex'
import mutations from './mutations'
import actions from './actions'
import getters from './getters'

Vue.use(Vuex); 

const state =  {
    newProjectPop:false, // 
    blankProjectPop:false, // 
    addPeoPlePop:false, // 
    itemRecord:'', //

    // yun
    details: false, // 
    taskTitle: '', // 
    taskStar: false, // 
    taskRemind: false, // 
    taskItem: null, // /
    taskList: [], // 
    taskIndex: [undefined, undefined], // 
    taskTimeState: '', // 
    taskTimeDate: '', // 
    taskDelChange: false, // 

    projectItem: null, // 

    // new_yun
    stageInfos: {}, // -
    checkedFileList: [], // 
    fileLength: 0, // 
    taskIds: {}, // ID
    power: 1, //  0-- 1--
    unreadNum: 0, // 
}



export default new Vuex.Store( {
    state, 
    getters, 
    actions, 
    mutations
}); 



