<template>
  <div class="setPwd">
    <div class="bgSvg_k">
      <div class="grid_ke"></div>
      <div class="svg"></div>
    </div>
    <div class="main_box">
      <div class="main">
        <!-- -->
        <div class="register" v-show="one">
          <p class="title"></p>
          <div class="pwd_div box">
            <span class="hint" v-show="hintPwd"></span>
            <input type="password" :placeholder="setPwPlace" class="inputBox" v-model="password"
                   v-on:blur="autofocusPw(password)" @focus="pawdFocus" v-on:input="pwdInput(password)" maxlength="20">
            <div class="" v-show="triangleShow">
              <span class="Triangle"></span>
              <div class="hintMessage">
                <p></p>
                <p>
                  <el-checkbox v-model="radio"> 6- 20</el-checkbox>
                </p>
                <p>
                  <el-checkbox v-model="radio2"></el-checkbox>
                </p>
              </div>
            </div>
          </div>

          <div class="pwd_div box">
            <span class="hint" v-show="hintPwdTwo"></span>
            <input type="password" :placeholder="setPwPlacetwo" class="inputBox" v-model="repPassword"
                   v-on:blur="autofocusPwTw(repPassword)" @focus="pawdFocusTwo" v-on:input="pwdInputTwo(repPassword)"
                   @keyup.enter="next(password)"
                   maxlength="20">
            <span class="error_title" v-if="erroPaw">{{erroPaw}}</span>
          </div>
          <div class="button">
            <span class="step" @click="next(password)"></span>
          </div>
        </div>

        <!---->
        <div class="setSucceed" v-show="two">
          <i class="el-icon-circle-check-outline"></i>
          <p class="title"></p>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { setCookie, getCookie } from "../../api/cookie";

export default {
  data() {
    return {
      email: "", //
      number: "", //
      hintEmail: false,
      hintPw: "",
      userName: "",
      passWord: "",
      checked: false,
      loginType: false, //  true false
      errorTitle: "", // 
      one: true,
      two: false,

      //     1   
      password: "",
      setPwPlace: "",
      setPwPlacetwo: "",
      repPassword: "",
      radio: false,
      radio2: false,
      triangleShow: false, //
      erroPaw: "",
      //      
      name: "",
      tell: "",
      hintPwd: false, //
      hintPwdTwo: false, // 
      perfectErro: "",
      hintName: false,
      hintTel: false
    };
  },

  watch: {
    userName(val) {
      if (this.errorTitle) {
        this.errorTitle = "";
      }
    },
    passWord(val) {
      if (this.errorTitle) {
        this.errorTitle = "";
      }
    }
  },
  mounted() {},

  methods: {
    //
    next(password) {
      if (this.password == "" || this.repPassword == "") {
        this.erroPaw = "";
        return;
      } else {
        this.erroPaw = "";
      }
      if (this.password !== this.repPassword) {
        this.erroPaw = "";
        return;
      } else {
        this.erroPaw = "";
      }

      //      .  
      if (this.erroPaw == "" && this.radio == true && this.radio2 == true) {
        this.one = false;
        this.two = true;

        let data = { Email: this.email, Pwd: this.password };
        this.$HTTP("post", "/RetrievUpdatePassword", data).then(res => {});
        var t;
        t = setTimeout(res => {
          this.$router.push("/login");
        }, 2000);
      } else {
        this.erroPaw = "";
      }
    },
    //      ==========================

    //      1.
    pawdFocus() {
      this.setPwPlace = "";
      this.erroPaw = "";
      this.hintPwd = true;
      this.triangleShow = true;
    },
    //     2.
    pwdInput(password) {
      let regExp = /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,21}$/;
      if (password.length >= 6) {
        this.radio = true;
      } else {
        this.radio = false;
      }
      if (regExp.test(password)) {
        this.radio2 = true;
      } else {
        this.radio2 = false;
        return false;
      }
    },
    //      3.
    autofocusPw() {
      this.setPwPlace = "";

      this.hintPwd = false;
      this.triangleShow = false;
      if (this.password == "") {
        this.erroPaw = "";
      }
    },
    //      
    autofocusPwTw() {
      this.setPwPlacetwo = "";

      this.setPwPlaceTwo = "";
      this.hintPwdTwo = false;
      if (this.password !== this.repPassword) {
        this.erroPaw = "";
        return;
      } else {
        this.erroPaw = "";
      }
      if (this.password == "") {
        this.erroPaw = "";
      }
    },
    //      
    pawdFocusTwo() {
      this.setPwPlacetwo = "";
      this.hintPwdTwo = true;
      this.erroPaw = "";
    },
    pwdInputTwo(pwd) {},

    setCookie(c_name, c_pwd, exdays) {
      var exdate = new Date(); //
      exdate.setTime(exdate.getTime() + 24 * 60 * 60 * 1000 * exdays); //
      //cookie
      window.document.cookie =
        "userName" + "=" + c_name + ";path=/;expires=" + exdate.toGMTString();
      window.document.cookie =
        "userPwd" + "=" + c_pwd + ";path=/;expires=" + exdate.toGMTString();
    },
    //cookie
    getCookie: function() {
      if (document.cookie.length > 0) {
        var arr = document.cookie.split("; "); //
        for (var i = 0; i < arr.length; i++) {
          var arr2 = arr[i].split("="); //
          //
          if (arr2[0] == "userName") {
            this.ruleForm.userName = arr2[1]; //
          } else if (arr2[0] == "userPwd") {
            this.ruleForm.password = arr2[1];
          }
        }
      }
    },
    //cookie
    clearCookie: function() {
      this.setCookie("", "", -1); //21
    }
  },
  created() {
    if (getCookie("loginEmail")) {
      this.$router.push("/myTesk/myTeskList");
      return
    }
    //      let url =window.location.href;
    let url = unescape(window.location.href).split("?")[1];

    this.email = url.split("&")[0].split("=")[1];
    console.log(this.email);
    this.number = url.split("&")[1].split("=")[1];
    if (this.number !== getCookie("number")) {
      this.$message(",");
      this.$router.push("/forgetPwd");
    }
    let t;
    t = setTimeout(function() {
      setCookie("number", "");
    }, 30000);
  }
};
</script>

<style lang="less">
@import "../../assets/css/base.less";
@import "../../assets/css/media.less";

.bgSvg_k {
  width: 1200px;
  height: 600px;
  position: absolute;
  left: 50%;
  margin-left: -600px;
  top: 50%;
  margin-top: -300px;
  .svg {
    width: 100%;
    height: 100%;
    position: absolute;
    background: url("../../assets/img/bg.svg");
  }
  .grid_ke {
    width: 100%;
    height: 100%;
    background: -webkit-linear-gradient(top, transparent 179px, #f0f0f0 180px),
      -webkit-linear-gradient(left, transparent 179px, #f0f0f0 180px);
    background-size: 180px 180px;
    position: absolute;
    z-index: 1;
  }
}

.setPwd {
  width: 100%;
  height: 100%;
  background: #ffffff;

  .main_box {
    height: calc(100vh - 20px);
    width: 100%;
    position: relative;
    .main {
      width: 317px;
      height: 200px;
      position: absolute;
      top: 50%;
      left: 50%;
      margin-left: -158px;
      margin-top: -100px;
      background: #ffffff;
      z-index: 9999;
      padding: 20px;
      .setSucceed {
        text-align: center;
        .el-icon-circle-check-outline {
          font-size: 40px;
          margin-bottom: 37px;
          color: #999999;
        }
      }
      .title {
        text-align: center;
        margin-bottom: 25px;
        font-size: 18px;
      }
      .box {
        height: 50px;
        position: relative;
        .hint {
          font-size: 12px;
          padding-left: 10px;
          position: absolute;
          bottom: 30px;
          color: #999999;
        }
        .inputBox {
          width: 317px;
          border-bottom: 1px solid #999999;
          padding: 0 10px 10px 10px;
          .box_sizing;
          position: absolute;
          bottom: 0;
        }
        .Triangle {
          left: 50%;
          margin-left: -5px;
          top: 40px;
          position: absolute;
          width: 10px;
          height: 10px;
          transform: rotate(45deg);
          -ms-transform: rotate(45deg); /* IE 9 */
          -moz-transform: rotate(45deg); /* Firefox */
          -webkit-transform: rotate(45deg); /* Safari  Chrome */
          -o-transform: rotate(45deg); /* Opera */
          background: #ffffff;
          border-left: 1px solid #e0e0e0;
          border-top: 1px solid #e0e0e0;
          z-index: 999;
        }
        .hintMessage {
          padding: 20px 20px 0 20px;
          .box_sizing;
          width: 192px;
          height: 138px;
          left: 50%;
          margin-left: -96px;
          top: 45px;
          border: 1px solid #e0e0e0;
          .border_radius(@br:5px);
          position: absolute;
          z-index: 998;
          font-size: 13px;
          background: #ffffff;
          p {
            margin-bottom: 15px;
          }
        }
      }
      .error_title {
        position: absolute;
        color: red;
        left: 10px;
        font-size: 12px;
        bottom: -20px;
      }
      .button {
        position: absolute;
        left: 50%;
        margin-left: -109px;
        width: 218px;
        height: 40px;
        line-height: 40px;
        text-align: center;
        margin-top: 40px;
        .step {
          text-align: center;
          line-height: 40px;
          display: inline-block;
          width: 40px;
          height: 40px;
          border-radius: 50%;
          border: 1px solid #ffffff;
          cursor: pointer;
        }
        .step:hover {
          border: 1px solid #333333;
        }
      }
    }
  }
}
</style>



